* {
    box-sizing: border-box;
    /*overflow: scroll;*/
}

.app_wrapper {
    display: grid;
    position: relative;
    width: 95%;
    min-width: 450px;
    margin: 0 auto;
    grid-template-areas: "header header" "nav content";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    transition: 0.5s all ease-out;
}

.app_wrapper_content {
    grid-area: content;
    background-color: darkorange;
}


@media (min-width: 1200px) {
    .app_wrapper{
        /*display: flex;*/
        max-width: 1600px;
    }

}
@media (max-width: 1200px) {
    .app_wrapper{
        /*display: flex;*/
        max-width: 992px;
    }

}

@media (max-width: 992px) {
    .app_wrapper{
        /*display: flex;*/
        max-width: 768px;
    }

}
@media (max-width: 768px) {
    .app_wrapper{
        /*display: flex;*/
        max-width: 95vh;
        min-width: 450px;
        display: grid;
        width: 95%;
        margin: 0px auto;
        grid-template-areas: "header" "nav" "content";
        grid-template-rows: 60px 1fr;
        grid-template-columns: 12fr;
    }

}
@media (max-width: 360px){
    .app_wrapper{
        /*display: flex;*/
        max-width: 95vh;
        min-width: 320px;
        display: grid;
        width: 95%;
        margin: 0px auto;
        grid-template-areas: "header" "nav" "content";
        grid-template-rows: 60px 1fr;
        grid-template-columns: 12fr;
    }

}

