*{
    box-sizing: border-box;
}
.header {
    grid-area: header;
    background-color: chartreuse;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.wrapper{
    display: flex;
    justify-content: left;
    align-items: center;
    flex:1 0 33.33%
}
@media (min-width: 768px){
    .burger_menu{
        display: none;
    }
}
@media (min-width: 360px){
    .btn_menu{
        /*display: none;*/
    }
}
/*hamburger menu*/
.burger_menu{
    width: 2rem;
    height: 2rem;
    /*background-color: white;*/
    /*color: cornflowerblue;*/
    cursor: pointer;
    border-radius: 20%;
    font-size: 1.5rem;
    text-align: center;
    /*margin:0px auto;*/
    padding: 2px 0px 0px 0px;

}

.wrapper_spacer{
    flex: 1;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}
/*user logOut*/
.selectMenu{
    width: content-box;
    height: 2rem;
    background-color: white;
    color: cornflowerblue;
    cursor: pointer;
    border-radius: 5px;
    font-size: 1.5rem;
    text-align: center;
    margin:0px auto;
    /*padding: 0px;*/
    padding: 2px 0px 0px 0px;
}
.logoBrandH2{
    padding: 1rem;
    font-family: "Freestyle Script";
    font-size: 2rem;
}
.userProfile{
    padding: 3px 0px;
    font-family: "Freestyle Script";
    font-size: 2rem;
}
.userProfile label{
    display: flex;
    align-items: center;
    padding: 5px;
}
.userName{
    padding: 0px 3px 0px 0px;
}

.wrapper_loginBlock{
    margin: 10px 0px 0px 0px;
    padding: 10px;
    float: right;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.wrapper_loginBlock a{
    color: black;
    font-size: larger;
}
.wrapper_logo{
    display: flex;
    align-items: center;
}
.logOut{
    width: content-box;
    height: 2rem;
    /*background-color: white;*/
    /*color: cornflowerblue;*/
    cursor: pointer;
    /*border-radius: 5px;*/
    font-size: 1.5rem;
    text-align: center;
    margin:0px auto;
    padding: 3px 0px 15px 0px;
}
