
.item{
  display: grid;
  grid-template-columns: 1fr 11fr;
  color: white;
  padding: 5px;
}
.item img{
  width: 50px;
  height: 50px;
  margin: 5px;
  border-radius: 50%;
}
.image_like{
  display: flex;
  flex-flow: column nowrap;
}
.message{
  display: flex;
  padding-left: 10px;
  align-items: center;
}

@media (max-width: 768px) {
  .item{
    padding-left: 10%;
    display: flex;
    flex-flow: nowrap row;
    justify-content: left;
  }
}
